import { Table } from "reactstrap";
import { Link } from "react-router-dom";
import "./Style.css";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import Sideprofile from "../components/Sideprofile";
import { Icon } from "@iconify/react";
import {
  fetchEmployeeInformation,
  fetchProbation,
  fetchWorkschedule,
  fetchJobtimeline,
  fetchSideprofile,
} from "../action/action";
const Tables = () => {
  const dispatch = useDispatch();
  const profile = useSelector((state) => state.profile.employeeinformtaion);
  const probation = useSelector((state) => state.profile.probation);
  const workschedule = useSelector((state) => state.profile.workschedule);
  const jobtimeline = useSelector((state) => state.profile.jobtimeline);
  const sideprofile = useSelector((state) => state.profile.sideprofile);

  useEffect(() => {
    // dispatch(fetchAttendance());
    dispatch(fetchEmployeeInformation());
    dispatch(fetchProbation());
    dispatch(fetchWorkschedule());
    dispatch(fetchJobtimeline());
    dispatch(fetchSideprofile());
  }, []);
  // if (!profile && !probation && !workschedule && !jobtimeline) {
  //   return <div>Loading...</div>;
  // }
  console.log("jobtimeline", jobtimeline);
  console.log(workschedule);
  console.log(probation);
  console.log(profile);
  if (!profile || !probation || !workschedule || !jobtimeline || !sideprofile) {
    // Render a loading state or return null while waiting for data
    return <div>Loading...</div>;
  }

  return (
    <>
      <div className="header">
        <div className="left-corner">
          <i className="user-icon "></i>
          <div>My Profile</div>
        </div>
      </div>
      <div className="wholeprofile" style={{ marginTop: "120px" }}>
        <Sideprofile/>
        <div>
          <div class="TabMenu__TabWrapper-sc-8owevb-0 jfSAHT ">
            <ul height="50px" class="Tab__Tabs-yx1443-0 jjUJid">
              <li class="Tab__TabItem-yx1443-1 gJenVO">
                <Link to="/profile">
                  {" "}
                  <a>GENERAL</a>
                </Link>
              </li>
              <li class="Tab__TabItem-yx1443-1 hjzHTJ">
                <a>Job</a>
              </li>
              <li class="Tab__TabItem-yx1443-1 gJenVO">
                <a>Documents</a>
              </li>
              <li class="Tab__TabItem-yx1443-1 gJenVO">
                <a>Dependents</a>
              </li>
              <li class="Tab__TabItem-yx1443-1 gJenVO">
                <a>Account Settings</a>
              </li>
            </ul>
          </div>

          <div class="section-container">
            <div class="header-container">
              <div class="flex-container">
                <Icon icon="uil:file-alt" color="#2ad25f" height={30} />
                <h5 class="section-title">Employe Information</h5>
              </div>
            </div>
            <form method="POST" class="form-view">
              <div class="grid-container">
                <div class="field-container">
                  <div>
                    <span class="field-title bJvZsc">Employee ID</span>
                  </div>
                  <div class="field-content">
                    <span class="field-value">{profile.employeeId}</span>
                  </div>
                  <div class="field-footer"></div>
                </div>
                <div class="field-container">
                  <div>
                    <span class="field-title bJvZsc">Join Date</span>
                  </div>
                  <div class="field-content">
                    <span class="field-value">{profile.joinDate}</span>
                  </div>
                  <div class="field-footer"></div>
                </div>
                <div class="field-container">
                  <div>
                    <span class="field-title bJvZsc">Service Year</span>
                  </div>
                  <div class="field-content">
                    <span class="field-value">{profile.serviceYear}</span>
                  </div>
                  <div class="field-footer"></div>
                </div>
              </div>
            </form>
          </div>
          <div class="section-container">
            <div class="header-container">
              <div class="flex-container">
                <Icon icon="uil:file-search-alt" color="#2ad258" height={30} />
                <h5 class="section-title">Probation Details</h5>
              </div>
            </div>
            <form method="POST" class="form-view">
              <div class="grid-container">
                <div class="field-container">
                  <div>
                    <span class="field-title bJvZsc">Probation Start Date</span>
                  </div>
                  <div class="field-content">
                    <span class="field-value">
                      {probation.probationStartDate}
                    </span>
                  </div>
                  <div class="field-footer"></div>
                </div>

                <div class="field-container">
                  <div>
                    <span class="field-title bJvZsc">Probation End Date</span>
                  </div>
                  <div class="field-content">
                    <span class="field-value">
                      {probation.probationEndDate}
                    </span>
                  </div>
                  <div class="field-footer"></div>
                </div>
                <div class="field-container">
                  <div>
                    <span class="field-title bJvZsc">Line Manager</span>
                  </div>
                  <div class="field-content">
                    <span class="field-value">{probation.lineManagerName}</span>
                  </div>
                  <div class="field-footer"></div>
                </div>
              </div>
            </form>
          </div>
          <div class="section-container">
            <div class="header-container">
              <div class="flex-container">
                <Icon
                  icon="material-symbols:history"
                  color="#2ad258"
                  height={30}
                />
                <h5 class="section-title">Job Timeline</h5>
              </div>
            </div>
            <form method="POST" class="form-view">
              <Table
                className="no-wrap mt-3 align-middle "
                responsive
                borderless
              >
                <thead>
                  <tr>
                    <th>Effective Date</th>
                    <th>Job Title</th>
                    <th>Position Type</th>
                    <th>Employment Type</th>
                    <th>Line Manager</th>
                    <th>Department</th>
                    <th>Office</th>
                    <th>Note</th>
                  </tr>
                </thead>
                <tbody>
                  <tr className="border-top">
                    <td className="mb-0">{jobtimeline.effectiveDate}</td>

                    <td>{jobtimeline.jobTitle}</td>
                    <td>{jobtimeline.positionType}</td>
                    <td></td>
                    <td>{jobtimeline.lineManagerName}</td>

                    <td className="scrollable-td">
                      {jobtimeline.departmentName}
                    </td>
                    <td>{jobtimeline.officeName}</td>
                    <td>{jobtimeline.note}</td>
                  </tr>
                </tbody>
              </Table>
            </form>
          </div>
          <div class="section-container">
            <div class="header-container">
              <div class="flex-container">
                <Icon icon="uil:calendar-alt" color="#2ad258" height={30} />
                <h5 class="section-title">Work Schedule</h5>
              </div>
            </div>
            <form method="POST" class="form-view">
              <div class="grid-container">
                <div class="field-container">
                  <div>
                    <span class="field-title bJvZsc">Bank Name</span>
                  </div>
                  <div class="field-content">
                    <span class="field-value">{}</span>
                  </div>
                  <div class="field-footer"></div>
                </div>

                <div class="field-container">
                  <div>
                    <span class="field-title bJvZsc">
                      Standard working hours/day
                    </span>
                  </div>
                  <div class="field-content">
                    <span class="field-value">
                      {workschedule.standardWorkingHoursPerDay}
                    </span>
                  </div>
                  <div class="field-footer"></div>
                </div>
                <div class="field-container">
                  <div>
                    <span class="field-title bJvZsc">Schedule type</span>
                  </div>
                  <div class="field-content">
                    <span class="field-value">{workschedule.scheduleType}</span>
                  </div>
                  <div class="field-footer"></div>
                </div>
                <div class="field-container">
                  <div>
                    <span class="field-title bJvZsc">
                      Total working hours/week
                    </span>
                  </div>
                  <div class="field-content">
                    <span class="field-value">
                      {workschedule.totalWorkingHoursPerWeek}
                    </span>
                  </div>
                  <div class="field-footer"></div>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  );
};

export default Tables;
